import { Component, h, Host, Prop } from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';

@Component({
  tag: 'tp-cartao-resposta-item',
  styleUrl: 'cartao-resposta-item.scss',
  shadow: true,
})
export class TpCartaoRespostaItem {
  /**
   * Define a variação de cor do componente.
   */
  @Prop({ reflect: true }) color: TpColor;

  /**
   * Define a estilização de cor do componente (line-through).
   */
  @Prop({ reflect: true }) canceled = false;

  /**
   * Define a estilização de cor do componente (background).
   */
  @Prop({ reflect: true }) printed = false;

  /**
   * Define a estilização de cor do componente (background com gradient).
   */
  @Prop({ reflect: true }) active = false;

  /**
   * Define a estilização de cor do componente (color warning).
   */
  @Prop({ reflect: true }) incorrect = false;

  /**
   * Define a estilização de cor do componente (color success).
   */
  @Prop({ reflect: true }) correct = false;

  /**
   * Define a estilização de cor do componente (color brand).
   */
  @Prop({ reflect: true }) discursive = false;

  render() {
    const { color, canceled, printed, active, incorrect, correct, discursive } =
      this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-cartao-resposta-item': true,
          'tp-cartao-resposta-item--canceled': canceled,
          'tp-cartao-resposta-item--printed': printed,
          'tp-cartao-resposta-item--active': active,
          'tp-cartao-resposta-item--incorrect': incorrect,
          'tp-cartao-resposta-item--correct': correct,
          'tp-cartao-resposta-item--discursive': discursive,
        })}
      >
        <div class="tp-cartao-resposta-item__container">
          <button class="tp-cartao-resposta-item__button">
            <slot></slot>
          </button>
        </div>
      </Host>
    );
  }
}
